
.horizontal {
  display: flex;
  flex-direction: row;
}

.piechart {
  width: 300px;
  height: 300px;
  margin: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.legend {
  margin: 1em;
}

.nodemap {
  height: 25em;
  width: 100%;
  margin-top: 0.5em;
  margin-bottom: 1em;
}

.histograms {
  display : inline-flex;
  width: 100%;
}

.half {
  width: 50%;
}

.timelineChart {
  margin: 20px 0;
  width: 100%;
  display: flex;
  justify-content: center;
}

.growthChart {
  width: 100%;
  max-width: 1000px;
  height: 500px;
  background-color: white;
  border-radius: 4px;
  padding: 10px;
}